<template>
  <div class="admin-page">
    <div class="admin-header">
      <h1>채팅 관리</h1>
      <div class="header-actions">
        <router-link to="/admin" class="nav-link">채팅 조회</router-link>
        <button class="btn-add" @click="openForm()">추가</button>
        <button class="btn-logout" @click="logout">로그아웃</button>
      </div>
    </div>

    <table class="systems-table">
      <thead>
        <tr>
          <th>System ID</th>
          <th>표시명</th>
          <th>Dify URL</th>
          <th>API Key</th>
          <th>활성</th>
          <th>관리</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="s in systems" :key="s.system_id">
          <td>{{ s.system_id }}</td>
          <td>{{ s.display_name || '-' }}</td>
          <td class="url-cell">{{ s.dify_base_url || '-' }}</td>
          <td class="key-cell">{{ maskKey(s.api_key) }}</td>
          <td>{{ s.enabled ? 'Y' : 'N' }}</td>
          <td>
            <button class="btn-edit" @click="openForm(s)">수정</button>
            <button class="btn-del" @click="confirmDelete(s)">삭제</button>
          </td>
        </tr>
      </tbody>
    </table>
    <p v-if="!loading && systems.length === 0" class="empty">등록된 시스템이 없습니다. '추가' 버튼으로 등록하세요.</p>

    <div v-if="showForm" class="modal-overlay" @click.self="closeForm">
      <div class="modal">
        <h3>{{ editing ? '시스템 수정' : '시스템 추가' }}</h3>
        <form @submit.prevent="save">
          <div class="form-row">
            <label>System ID</label>
            <input v-model="form.system_id" required :disabled="editing" placeholder="e.g. drillquiz" />
            <span v-if="editing" class="hint">수정 불가</span>
          </div>
          <div class="form-row">
            <label>표시명</label>
            <input v-model="form.display_name" placeholder="e.g. DrillQuiz" />
          </div>
          <div class="form-row">
            <label>Dify Base URL</label>
            <input v-model="form.dify_base_url" required placeholder="https://api.dify.ai/v1" />
          </div>
          <div class="form-row">
            <label>Dify API Key</label>
            <input v-model="form.dify_api_key" type="password" required placeholder="app-xxx" />
          </div>
          <div class="form-row">
            <label>Gateway API Key</label>
            <input v-model="form.api_key" type="password" required placeholder="key_xxx" />
          </div>
          <div class="form-row">
            <label>Allowed Origins (쉼표 구분)</label>
            <input v-model="form.allowed_origins" placeholder="https://example.com,https://app.example.com" />
          </div>
          <div class="form-row">
            <label><input v-model="form.enabled" type="checkbox" /> 활성</label>
          </div>
          <p v-if="formError" class="error">{{ formError }}</p>
          <div class="form-actions">
            <button type="button" class="btn-cancel" @click="closeForm">취소</button>
            <button type="submit" class="btn-save" :disabled="saving">{{ saving ? '저장 중...' : '저장' }}</button>
          </div>
        </form>
      </div>
    </div>

    <div v-if="deleteTarget" class="modal-overlay" @click.self="deleteTarget = null">
      <div class="modal">
        <h3>삭제 확인</h3>
        <p>{{ deleteTarget.system_id }}를 삭제하시겠습니까?</p>
        <div class="form-actions">
          <button type="button" class="btn-cancel" @click="deleteTarget = null">취소</button>
          <button type="button" class="btn-del" @click="doDelete" :disabled="deleting">{{ deleting ? '삭제 중...' : '삭제' }}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const TOKEN_KEY = 'admin_token'
const API = '/v1/admin/systems'

export default {
  name: 'AdminSystems',
  data() {
    return {
      systems: [],
      loading: false,
      showForm: false,
      editing: null,
      form: {
        system_id: '',
        display_name: '',
        dify_base_url: '',
        dify_api_key: '',
        api_key: '',
        allowed_origins: '',
        enabled: true
      },
      formError: '',
      saving: false,
      deleteTarget: null,
      deleting: false
    }
  },
  mounted() {
    const token = localStorage.getItem(TOKEN_KEY)
    if (!token) {
      this.$router.replace('/login')
      return
    }
    this.load()
  },
  methods: {
    authHeaders() {
      const token = localStorage.getItem(TOKEN_KEY)
      return token ? { 'Authorization': 'Bearer ' + token } : {}
    },
    async load() {
      this.loading = true
      try {
        const r = await fetch(API, { headers: this.authHeaders() })
        if (r.status === 401) {
          localStorage.removeItem(TOKEN_KEY)
          this.$router.replace('/login')
          return
        }
        if (!r.ok) throw new Error(await r.text())
        this.systems = await r.json()
      } catch (e) {
        this.formError = e.message
      } finally {
        this.loading = false
      }
    },
    maskKey(k) {
      if (!k) return '-'
      if (k.length <= 8) return '***'
      return k.slice(0, 4) + '...' + k.slice(-4)
    },
    openForm(s) {
      this.editing = s || null
      this.formError = ''
      if (s) {
        this.form = {
          system_id: s.system_id,
          display_name: s.display_name || '',
          dify_base_url: s.dify_base_url || '',
          dify_api_key: s.dify_api_key || '',
          api_key: s.api_key || '',
          allowed_origins: s.allowed_origins || '',
          enabled: s.enabled
        }
      } else {
        this.form = {
          system_id: '',
          display_name: '',
          dify_base_url: '',
          dify_api_key: '',
          api_key: '',
          allowed_origins: '',
          enabled: true
        }
      }
      this.showForm = true
    },
    closeForm() {
      this.showForm = false
      this.editing = null
    },
    async save() {
      this.formError = ''
      this.saving = true
      try {
        if (this.editing) {
          const r = await fetch(API + '/' + encodeURIComponent(this.editing.system_id), {
            method: 'PATCH',
            headers: { ...this.authHeaders(), 'Content-Type': 'application/json' },
            body: JSON.stringify({
              display_name: this.form.display_name,
              dify_base_url: this.form.dify_base_url,
              dify_api_key: this.form.dify_api_key,
              api_key: this.form.api_key,
              allowed_origins: this.form.allowed_origins,
              enabled: this.form.enabled
            })
          })
          if (r.status === 401) {
            localStorage.removeItem(TOKEN_KEY)
            this.$router.replace('/login')
            return
          }
          if (!r.ok) {
            const d = await r.json().catch(() => ({}))
            throw new Error(d.detail || '수정 실패')
          }
        } else {
          const r = await fetch(API, {
            method: 'POST',
            headers: { ...this.authHeaders(), 'Content-Type': 'application/json' },
            body: JSON.stringify(this.form)
          })
          if (r.status === 401) {
            localStorage.removeItem(TOKEN_KEY)
            this.$router.replace('/login')
            return
          }
          if (!r.ok) {
            const d = await r.json().catch(() => ({}))
            throw new Error(d.detail || '추가 실패')
          }
        }
        this.closeForm()
        await this.load()
      } catch (e) {
        this.formError = e.message
      } finally {
        this.saving = false
      }
    },
    confirmDelete(s) {
      this.deleteTarget = s
      this.deleting = false
    },
    async doDelete() {
      if (!this.deleteTarget) return
      this.deleting = true
      try {
        const r = await fetch(API + '/' + encodeURIComponent(this.deleteTarget.system_id), {
          method: 'DELETE',
          headers: this.authHeaders()
        })
        if (r.status === 401) {
          localStorage.removeItem(TOKEN_KEY)
          this.$router.replace('/login')
          return
        }
        if (!r.ok) throw new Error(await r.text())
        this.deleteTarget = null
        await this.load()
      } catch (e) {
        this.formError = e.message
      } finally {
        this.deleting = false
      }
    },
    logout() {
      localStorage.removeItem(TOKEN_KEY)
      this.$router.replace('/login')
    }
  }
}
</script>

<style scoped>
.admin-page { padding: 1rem; max-width: 1200px; margin: 0 auto; }
.admin-header { display: flex; justify-content: space-between; align-items: center; margin-bottom: 1rem; flex-wrap: wrap; gap: 0.5rem; }
.admin-header h1 { margin: 0; font-size: 1.25rem; }
.header-actions { display: flex; align-items: center; gap: 0.5rem; }
.nav-link { padding: 0.4rem 0.75rem; background: #334155; color: #94a3b8; text-decoration: none; border-radius: 6px; font-size: 0.9rem; }
.nav-link:hover { color: #38bdf8; background: #475569; }
.btn-add { padding: 0.4rem 0.75rem; background: #2563eb; color: #fff; border: none; border-radius: 6px; cursor: pointer; }
.btn-logout { padding: 0.4rem 0.75rem; background: #64748b; color: #fff; border: none; border-radius: 6px; cursor: pointer; }
.systems-table { border-collapse: collapse; width: 100%; }
.systems-table th, .systems-table td { border: 1px solid #475569; padding: 0.4rem 0.6rem; text-align: left; }
.systems-table th { background: #334155; color: #94a3b8; }
.url-cell { max-width: 200px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
.key-cell { font-family: monospace; font-size: 0.85em; }
.btn-edit { padding: 0.25rem 0.5rem; background: #475569; color: #fff; border: none; border-radius: 4px; cursor: pointer; margin-right: 0.25rem; }
.btn-del { padding: 0.25rem 0.5rem; background: #dc2626; color: #fff; border: none; border-radius: 4px; cursor: pointer; }
.empty { color: #94a3b8; }
.modal-overlay { position: fixed; inset: 0; background: rgba(0,0,0,0.6); display: flex; align-items: center; justify-content: center; z-index: 200; }
.modal { background: #1e293b; border-radius: 12px; padding: 1.5rem; min-width: 400px; max-width: 90vw; }
.modal h3 { margin: 0 0 1rem; }
.form-row { margin-bottom: 1rem; }
.form-row label { display: block; margin-bottom: 0.25rem; font-size: 0.9rem; color: #94a3b8; }
.form-row input[type="text"],
.form-row input[type="password"] { width: 100%; padding: 0.5rem; border: 1px solid #475569; border-radius: 6px; background: #0f172a; color: #f1f5f9; }
.form-row input:disabled { opacity: 0.6; }
.hint { font-size: 0.8rem; color: #64748b; margin-left: 0.5rem; }
.form-actions { display: flex; gap: 0.5rem; justify-content: flex-end; margin-top: 1rem; }
.btn-cancel { padding: 0.4rem 0.75rem; background: #475569; color: #fff; border: none; border-radius: 6px; cursor: pointer; }
.btn-save { padding: 0.4rem 0.75rem; background: #2563eb; color: #fff; border: none; border-radius: 6px; cursor: pointer; }
.error { color: #f87171; font-size: 0.9rem; }
</style>
